import {getaddress,createorder} from "../../apis"
export default {
	data() {
		return {
			token:"",
			infolist:"",
			datalist:"",
			total:0,
			a:""
		}
	},
	methods: {
		create(){
			createorder({
				goodsJsonStr:this.a,
				token:this.token
			}).then(res=>{
				let [err,data]=res;
				console.log(data)
				console.log(this.a)
				uni.showToast({
					title:"创建订单成功"
				})
				uni.navigateTo({
					url:"../user/user"
				})
			})
		}
	},
	onLoad(options) {
		try {
		    const value = uni.getStorageSync('token');
		    if (value!=="") {
		        this.token=value.token
				getaddress({
					token:this.token
				}).then(res=>{
					let [err,data]=res
					this.msg=data.data.msg
					this.infolist=(data.data.data)[0]
				})	
		    }
		} catch (e) {
		    // error
		}
		this.a=options.index
		var data = JSON.parse(options.index)
		this.datalist=data;
		for(let i=0;i<this.datalist.length;i++){
			this.total+=Number(this.datalist[i].number)*Number(this.datalist[i].price)  
		}
	}
}
